import {
  createCurrentMonthData,
  createLabelName
} from './generateUtils'

const generateSheet1 = (result3, total, workDates, isCalcWorkDays) => {
  const workDays = workDates.length;
  const { year, month, last: lastDay, fullMonth, fullYear } = createCurrentMonthData()
  const sheet2LastLabel = createLabelName(lastDay + 3)
  const generate = (result3, total, workDays, isCalcWorkDays) => {
    let merges = [
      {
        s: { c: 0, r: 0 },
        e: { c: 18, r: 0 }
      }, {
        s: { c: 0, r: 1 },
        e: { c: 18, r: 1 }
      }
    ]
    let wbData = {}
    const wbData2 = {}
    const wbData3 = {}
    let departmentMerge = 3
    let groupMerge = 3
    let no = 1
    let noIndex = 4

    const generateSheet1Data = (item) => {
      wbData[`A${noIndex}`] = {
        v: no,
        t: 'n',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }

      wbData[`D${noIndex}`] = {
        v: `${item.name}`,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }

      wbData[`E${noIndex}`] = {
        v: item.workDays,
        t: 'n',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }

      wbData[`F${noIndex}`] = {
        v: `${(isCalcWorkDays && item.workDays === workDays) ? '√' : ''}`,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }

      if (item.subsidy) {
        wbData[`G${noIndex}`] = {
          v: `${item.subsidy}元`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        }
      }

      if (item.overtime) {
        wbData[`H${noIndex}`] = {
          v: `${item.overtime}分钟`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        }
      }

      if (item.late) {
        wbData[`I${noIndex}`] = {
          v: `${item.late}分钟`,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        }

        const note = Object.entries(item.data).reduce((note, [time, { late }]) => {
          const [year, month, date] = time.trim().split('/')

          if (late) {
            note += `${date}日迟到${late}分钟，`;
          }
          return note;
        }, '迟到：');

        wbData[`R${noIndex}`] = {
          v: note,
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center',
              wrapText: true,
            }
          }
        }
      }
    }
    const generateSheet2Data = item => {
      const index = noIndex - 1

      wbData2[`A${index}`] = {
        v: `${item.name}`,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          },
          fill: {
            fgColor: {
              rgb: 'FF9FCD63'
            }
          }
        }
      }

      if (item.late != null) {
        wbData2[`${sheet2LastLabel}${index}`] = {
          v: item.late,
          t: 'n',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        }
      }

      Object.entries(item.data).forEach(([time, { late }]) => {
        const date = +time.split('/')[2]

        if (late) {
          wbData2[`${createLabelName(date + 1)}${index}`] = {
            v: late,
            t: 'n',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          }
        }
      })
    }
    const generateSheet3Data = item => {
      const index = noIndex - 1

      wbData3[`A${index}`] = {
        v: `${item.name}`,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          },
          fill: {
            fgColor: {
              rgb: 'FF9FCD63'
            }
          }
        }
      }
    }

    for (let department in result3) {
      for (let key in result3[department]) {
        let value = result3[department][key]

        if (key == 'count') {
          merges.push(
            {
              s: { c: 1, r: departmentMerge },
              e: { c: 1, r: departmentMerge + value - 1 }
            }
          )

          wbData[`B${departmentMerge + 1}`] = {
            v: department,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          }
          departmentMerge += value
        } else {
          let group = key;
          const count = value.length;

          merges.push(
            {
              s: { c: 2, r: groupMerge },
              e: { c: 2, r: groupMerge + count - 1 }
            }
          )

          wbData[`C${groupMerge + 1}`] = {
            v: group,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          }
          groupMerge += count

          value.forEach((employee) => {
            generateSheet1Data(employee)
            generateSheet2Data(employee)
            generateSheet3Data(employee)

            no++
            noIndex++
          });

          // for (let key2 in value) {
          //   let value2 = value[key2]

          //   if (key2 === 'count') {
          //     merges.push(
          //       {
          //         s: { c: 2, r: groupMerge },
          //         e: { c: 2, r: groupMerge + value2 - 1 }
          //       }
          //     )

          //     wbData[`C${groupMerge + 1}`] = {
          //       v: group,
          //       t: 's',
          //       s: {
          //         alignment: {
          //           vertical: 'center',
          //           horizontal: 'center'
          //         }
          //       }
          //     }
          //     groupMerge += value2
          //   } else {
          //     generateSheet1Data(value2)
          //     generateSheet2Data(value2)
          //     generateSheet3Data(value2)

          //     no++
          //     noIndex++
          //   }
          // }
        }
      }
    }

    for (let i = 1; i <= lastDay; i++) {
      const timeLabel = `${year}.${month}.${i}`;
      const rowName = createLabelName(i + 1);
      wbData2[`${rowName}2`] = {
        v: timeLabel,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }

      // 非工作日
      if (!workDates.map((item => {
        return item.slice(2).replace(/\//g, '.');
      })).includes(timeLabel)) {
        for (let j = 2; j <= no + 1; j++) {
          const value = j === 2 ? timeLabel : '';
          wbData2[`${rowName}${j}`] = {
            v: value,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              },
              fill: {
                fgColor: {
                  rgb: 'FFC8D5A1'
                }
              }
            }
          }
        }
      }

      wbData3[`${rowName}2`] = {
        v: timeLabel,
        t: 's',
        s: {
          alignment: {
            vertical: 'center',
            horizontal: 'center'
          }
        }
      }
    }

    wbData2[`${sheet2LastLabel}2`] = {
      v: '总计（分钟）',
      t: 's',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        }
      }
    }
    wbData3[`${sheet2LastLabel}2`] = {
      v: '总计（小时）',
      t: 's',
      s: {
        alignment: {
          vertical: 'center',
          horizontal: 'center'
        }
      }
    }

    merges.push({
      s: { c: 0, r: total + 3 },
      e: { c: 18, r: total + 3 }
    });

    let sheet1Cols = []
    for (let i = 1; i <= 19; i++) {
      sheet1Cols.push({
        wpx: 95
      })
    }

    return {
      SheetNames: ['汇总', '明细', '休病假'],
      Sheets: {
        '汇总': {
          '!ref': `A1:S${total + 4}`,
          '!merges': merges,
          '!cols': sheet1Cols,
          ...wbData,
          A1: {
            v: `广州汉风科技有限公司考勤表（${month}月份）`,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              },
              font: {
                sz: '18'
              }
            }
          },
          A2: {
            v: `              负责人签字:                       ${fullYear}/${month}/${lastDay}`,
            t: 's',
            s: {
              alignment: {
                horizontal: 'top'
              },
              font: {
                sz: '10'
              }
            }
          },
          A3: {
            v: '序号',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          [`A${total + 4}`]: {
            v: '备注：每月每人100元全勤奖励（已经走邮件批复）。',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
              }
            }
          },
          B3: {
            v: '部门',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          C3: {
            v: '组别',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          D3: {
            v: '姓名',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          E3: {
            v: '出勤',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          F3: {
            v: '全勤',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          G3: {
            v: '餐补',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          H3: {
            v: '加班',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          I3: {
            v: `${month}月累计迟到`,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          J3: {
            v: '上班未打卡',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          K3: {
            v: `${month}月累计早退`,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          L3: {
            v: '下班未打卡',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          M3: {
            v: `${month}月累计旷工`,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          N3: {
            v: `${month}月累计事假`,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          O3: {
            v: `${month}月累计病假`,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          P3: {
            v: `${month}月累计年假`,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          Q3: {
            v: `${month}月累计有薪假`,
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          R3: {
            v: '备注',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          S3: {
            v: '员工签名',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          }
        },

        '明细': {
          '!ref': `A1:${sheet2LastLabel}${total + 2}`,
          '!merges': [
            {
              s: { c: 0, r: 0 },
              e: { c: lastDay + 2, r: 0 }
            }
          ],
          A1: {
            v: '广州汉风员工迟到登记表',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          ...wbData2
        },

        '休病假': {
          '!ref': `A1:${sheet2LastLabel}${total + 2}`,
          '!merges': [
            {
              s: { c: 0, r: 0 },
              e: { c: lastDay + 2, r: 0 }
            }
          ],
          A1: {
            v: '广州汉风员工休病假登记表',
            t: 's',
            s: {
              alignment: {
                vertical: 'center',
                horizontal: 'center'
              }
            }
          },
          ...wbData3
        }
      }
    }
  };
  const departmentSheet = Object.entries(result3).reduce((departmentSheet, [department, groups]) => {
    const sheetName = department == '' ? '已离职员工' : department;
    const obj = {
      [department]: groups
    };
    departmentSheet.push({
      sheet: generate(obj, total, workDays, isCalcWorkDays),
      name: `汉风科技${sheetName}${month}月考勤表`
    });
    return departmentSheet;
  }, []);

  return {
    sheet1: generate(result3, total, workDays, isCalcWorkDays),
    departmentSheet
  }
}

export default generateSheet1
