import {
  createCurrentMonthData,
  createLabelName
} from './generateUtils'

const generateSheet2 = (result3) => {
  let sheet4Merges = []
  const wbData4 = {}
  let techCount = 0
  const { year, month, last: lastDay, fullMonth, fullYear } = createCurrentMonthData()
  const generateSheet4Data = item => {
    const no = techCount + 1
    const index = techCount + 6

    wbData4[`A${index}`] = {
      v: no,
      t: 'n',
      s: {
        alignment: {
          horizontal: 'center'
        }
      }
    }

    wbData4[`B${index}`] = {
      v: `${item.name}`,
      t: 's',
      s: {
        alignment: {
          horizontal: 'center'
        }
      }
    }

    sheet4Merges.push({
      s: { c: 1, r: techCount + 5 },
      e: { c: 2, r: techCount + 5 }
    })

    wbData4[`${createLabelName(lastDay + 4)}${index}`] = {
      v: parseFloat(`${(item.overtime / 60).toFixed(1)}`),
      t: 'n',
      s: {
        alignment: {
          horizontal: 'center'
        }
      }
    }

    if (item.subsidy > 0) {
      wbData4[`${createLabelName(lastDay + 5)}${index}`] = {
        v: `${(item.subsidy).toFixed(0)}元`,
        t: 's',
        s: {
          alignment: {
            horizontal: 'center'
          }
        }
      }
    }

    Object.entries(item.data).forEach(([time, { overtime, signOff }]) => {
      const date = +time.split('/')[2]

      if (overtime != null && overtime > 0) {
        let s = {
          s: {
            alignment: {
              horizontal: 'center',
              wrapText: true,
            }
          }
        }

        if (overtime <= 60) {
          s.s.font = {
            color: {
              rgb: 'FFFF0000'
            }
          }
        }

        wbData4[`${createLabelName(date + 3)}${index}`] = {
          v: `${(overtime / 60).toFixed(1)}（${signOff}）`,
          t: 's',
          ...s
        }
      }
    })
  }

  for (let departmentName in result3) {
    for (let groupName in result3[departmentName]) {
      let employees = result3[departmentName][groupName]

      if (groupName !== 'count') {
        employees.forEach((employee) => {
          if (departmentName == '技术部') {
            generateSheet4Data(employee)
            techCount++
          }
        });
      }
    }
  }

  for (let i = 1; i <= lastDay; i++) {
    wbData4[`${createLabelName(i + 3)}5`] = {
      v: `${i}日`,
      t: 's',
      s: {
        alignment: {
          horizontal: 'center'
        }
      }
    }
  }

  let sheet4Cols = []
  for (let i = 1; i <= lastDay + 5; i++) {
    let wpx = i >= lastDay + 4 ? 90 : 40

    sheet4Cols.push({
      wpx
    })
  }

  return {
    SheetNames: ['Sheet1'],
    Sheets: {
      'Sheet1': {
        '!ref': `A1:${createLabelName(lastDay + 5)}${techCount + 14}`,
        '!cols': sheet4Cols,
        '!merges': [
          {
            s: { c: 0, r: 0 },
            e: { c: lastDay + 4, r: 1 }
          },
          {
            s: { c: 0, r: 2 },
            e: { c: 12, r: 2 }
          },
          {
            s: { c: 13, r: 2 },
            e: { c: lastDay - 1, r: 2 }
          },
          {
            s: { c: 3, r: 3 },
            e: { c: lastDay + 4, r: 3 }
          },
          {
            s: { c: lastDay, r: 2 },
            e: { c: lastDay + 4, r: 2 }
          },
          {
            s: { c: 0, r: techCount + 5 },
            e: { c: lastDay + 4, r: techCount + 7 }
          },
          {
            s: { c: lastDay + 3, r: techCount + 8 },
            e: { c: lastDay + 4, r: techCount + 10 }
          },
          {
            s: { c: 0, r: techCount + 8 },
            e: { c: lastDay + 2, r: techCount + 10 }
          },
          {
            s: { c: 0, r: techCount + 11 },
            e: { c: 12, r: techCount + 13 }
          },
          {
            s: { c: lastDay, r: techCount + 11 },
            e: { c: lastDay + 4, r: techCount + 13 }
          },
          {
            s: { c: 13, r: techCount + 11 },
            e: { c: lastDay - 1, r: techCount + 13 }
          },
          ...sheet4Merges
        ],
        'A1': {
          v: '加班申请表',
          t: 's',
          s: {
            alignment: {
              vertical: 'center',
              horizontal: 'center'
            }
          }
        },
        'A3': {
          v: '申请部门：技术部',
          t: 's'
        },
        'N3': {
          v: '申请人：李冬杰',
          t: 's'
        },
        [`${createLabelName(lastDay + 1)}3`]: {
          v: `申请日期：${fullYear}${fullMonth}01-${fullMonth}${lastDay - 1}`,
          t: 's',
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        },
        'D4': {
          v: `${fullYear}年${month}月`,
          t: 's',
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        },
        [`${createLabelName(lastDay + 4)}5`]: {
          v: '合计（小时）',
          t: 's',
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        },
        [`${createLabelName(lastDay + 5)}5`]: {
          v: '餐补（元）',
          t: 's',
          s: {
            alignment: {
              horizontal: 'center'
            }
          }
        },
        [`A${techCount + 6}`]: {
          v: '加班原因（具体工作内容）：',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        [`${createLabelName(lastDay + 4)}${techCount + 9}`]: {
          v: '申请人签字：  ',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        [`A${techCount + 12}`]: {
          v: '部门负责人审批：                    ',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        [`N${techCount + 12}`]: {
          v: '行政部：                 ',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        [`${createLabelName(lastDay + 1)}${techCount + 12}`]: {
          v: '总经理：                    ',
          t: 's',
          s: {
            alignment: {
              vertical: 'top'
            }
          }
        },
        ...wbData4
      }
    }
  }
}

export default generateSheet2
