import getV from './getV'

const formatTime = time => {
  const [ hour, minite ] = time.trim().split(':')
  return parseInt(hour) * 60 + parseInt(minite) * 1
}
const getLateData = ({ time, date, workDates }) => {
  if (!workDates.includes(date)) {
    return {
      isLate: false,
      late: 0
    }
  }

  const limit = 4
  const lateTime = formatTime(time)
  const isLate = lateTime > limit

  return {
    isLate,
    late: isLate ? (lateTime - limit) : 0
  }
}
const getOvertimeData = ({ time, date, workDates }) => {
  if (!workDates.includes(date)) {
    return {
        isOvertime: false,
        time: 0,
        subsidyTime: 0
    }
  }

  const subsidyLimit = 1.5 * 60 - 20 // 8点40后技术部有补贴
  const overtimeLimit = 1 * 60 // 7点半后算加班
  let overtime = formatTime(time) - overtimeLimit
  const subsidyTime = overtime - subsidyLimit
  const isOvertime = overtime > 0

  if (overtime >= (2 * 60 + 10) && overtime <= (2 * 60 + 30)) {
    overtime = 2 * 60 + 30;
  }

  return {
    isOvertime,
    time: overtime > 0 ? overtime : 0,
    subsidyTime: subsidyTime > 0 ? subsidyTime : 0
  }
}

const format = (sheetData, workDates) => {
  const start = 2
  const end = sheetData['!range'].e.r

  let result = {}

  for (let i = start; i <= end; i++) {
    let id = getV(sheetData[`B${i}`]),
      name = getV(sheetData[`C${i}`]),
      date = getV(sheetData[`D${i}`]),
      timeType = getV(sheetData[`E${i}`]),
      signIn = getV(sheetData[`H${i}`]),
      signOff = getV(sheetData[`I${i}`]),
      lateTime = getV(sheetData[`L${i}`]),
      overtime = getV(sheetData[`O${i}`])

    if (!result[id]) {
      result[id] = {
        id: id,
        name: name,
        department: '',
        late: 0,
        overtime: 0,
        subsidy: 0,
        data: {},
        workDays: 0
      }
    }

    result[id].data[date] = {
      date: date,
      timeType: timeType,
      signIn: signIn,
      signOff: signOff,
      rawLateTime: lateTime,
      overtime: overtime,
      subsidy: 0,
      subsidyTime: 0,
      needWork: workDates.includes(date),
      isWork: !!signOff
    }

    // 迟到
    if (lateTime) {
      const { isLate, late } = getLateData({
        time: lateTime,
        date,
        workDates
      })
      result[id].data[date].isLate = isLate
      result[id].data[date].late = late
      result[id].late += late
    }

    if (workDates.includes(date) && (signIn || signOff)) {
      result[id].workDays++;
    }

    // 加班
    if (overtime) {
      const { isOvertime, time, subsidyTime } = getOvertimeData({
        time: overtime,
        date,
        workDates
      })
      const subsidyPerDay = 20
      const subsidy = subsidyTime > 0 ? subsidyPerDay : 0

      result[id].data[date].isOvertime = isOvertime
      result[id].data[date].overtime = time
      result[id].data[date]._subsidyTime = subsidyTime
      result[id].data[date]._subsidy = subsidy
      result[id].overtime += time
    }
  }

  return result
}

export default format
