import generateSheet1 from './generateSheet1'
import generateSheet2 from './generateSheet2'

const generate = (result3, total, workDates, isCalcWorkDays) => {
  const { sheet1, departmentSheet } = generateSheet1(result3, total, workDates, isCalcWorkDays);
  return {
    sheet1,
    departmentSheet,
    sheet2: generateSheet2(result3),
  }
};

export default generate;