const getDepartment = data => {
  return data ? data.department : ''
}
const getGroup = data => {
  return data ? data.group : ''
}
const getIndex = data => {
  return data ? data.index : ''
}

const getDepartmentInfo = ({ id, name, data }) => {
  let department, group, index;
  const staffInfo = data.find((item) => {
    if (name == '刘莉') {
      return item.name === name && item.id === id;
    } else {
      return item.name === name;
    }
  });

  department = getDepartment(staffInfo);
  group = getGroup(staffInfo);
  index = getIndex(staffInfo);

  return {
    department,
    group,
    index,
  }
};

const sortResult = (totalObj) => {
  let nullTmp;

  if (totalObj[``]) {
    nullTmp = { ... totalObj[``] };
    delete nullTmp[``].count;
    delete totalObj[``];
  }

  for (let department in totalObj) {
    if (department != '') {
      for (let key in totalObj[department]) {
        if (key !== 'count' && key != '') {
          let employees = totalObj[department][key];
          delete employees.count;

          let sortedEmployees = Object.values(employees).sort((a, b) => {
            return a._index - b._index;
          });

          totalObj[department][key] = sortedEmployees;
        }
      }
    }
  }

  if (nullTmp) {
    totalObj[``] = {
      [``]: Object.values(nullTmp[``]),
      count: nullTmp.count,
    };
  }
  return totalObj;
};

const format = (result, result2) => {
  let result3 = {}
  let total = 0

  for (let id in result) {
    const name = result[id].name;
    let { department, group, index } = getDepartmentInfo({ id, name, data: result2})
    group = group || department

    result[id].department = department
    result[id].group = group
    result[id]._index = index

    if (department === '技术部') {
      for (let date in result[id].data) {
        let subsidy = result[id].data[date]._subsidy
        let subsidyTime = result[id].data[date]._subsidyTime

        if (subsidy > 0) {
          result[id].data[date].subsidy = subsidy
          result[id].data[date].subsidyTime = subsidyTime
          result[id].subsidy += subsidy
          result[id].subsidyTime += subsidyTime
        }
      }
    } else {
      result[id].overtime = 0;
    }

    if (!result3[department]) {
      result3[department] = {
        count: 0
      }
    }

    if (!result3[department][group]) {
      result3[department][group] = {
        count: 0
      }
    }

    result3[department][group][id] = result[id]
    result3[department].count++
    result3[department][group].count++
    total++
  }

  return {
    total,
    data: sortResult(result3)
  }
}

export default format
