<template>
  <div id="app">
    <el-row :gutter="20">
      <el-col :span="8">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>选择工作日期</span>
          </div>
          <div class="text item">
            <el-date-picker
              type="dates"
              v-model="dates"
              size="large"
              placeholder="选择工作日">
            </el-date-picker>
            <el-checkbox v-model="isCalcWorkDays" style="margin-left: 20px;">显示全勤</el-checkbox>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>上传报表</span>
          </div>
          <div class="text item">
            <el-upload
              class="upload-item"
              action=""
              :on-change="onUploadChange1"
              :auto-upload="false">
              <el-button slot="trigger" size="medium" type="primary">选取考勤报表（请确保后缀名是 xls）</el-button>
            </el-upload>
            <el-upload
              class="upload-item"
              action=""
              :on-change="onUploadChange2"
              :auto-upload="false">
              <el-button slot="trigger" size="medium" type="primary">选取员工花名册（请确保后缀名是 xlsx）</el-button>
            </el-upload>
            <el-button size="medium" type="primary" @click="onStart" :disabled="isProcessing">开始生成</el-button>
            <div style="margin-top: 60px" v-if="isFinish">
              <el-button size="medium" type="primary" @click="downloadSummarySheet">下载总表</el-button>
              <el-button size="medium" type="primary" @click="downloadDepartmentSheet">下载部门表</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="8">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>注意事项</span>
          </div>
          <div class="text item">
            1、检查数据正确性
          </div>
          <div class="text item">
            2、修改时间
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import getSheetData from '@/utils/getSheetData'
import formatSheet1 from '@/utils/formatSheet1'
import formatSheet2 from '@/utils/formatSheet2'
import updateDepartment from '@/utils/updateDepartment'
import generateSheet from '@/utils/generateSheet'
import save from '@/utils/save'

const today = new Date();

export default {
  name: 'app',

  data () {
    return {
      dates: [],
      file1: null,
      file2: null,
      sheet1: null,
      sheet2: null,
      departmentSheet: null,
      isFinish: false,
      year: today.getFullYear(),
      month: today.getMonth() + 1,
      isCalcWorkDays: true,
      isProcessing: false,
    }
  },

  methods: {
    onStart () {
      if (this.dates.length === 0) {
        this.$alert('请选择工作日期', '提醒', {
          confirmButtonText: '确定',
        });
        return;
      }

      if (this.file1 == null) {
        this.$alert('请上传考勤报表', '提醒', {
          confirmButtonText: '确定',
        });
        return;
      }

      if (this.file2 == null) {
        this.$alert('请上传花名册', '提醒', {
          confirmButtonText: '确定',
        });
        return;
      }

      if (this.isProcessing) {
        return;
      }

      this.isProcessing = true;

      const workDates = this.dates.map(date => {
        return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
      })

      Promise.all([
        getSheetData(this.file1),
        getSheetData(this.file2)
      ]).then(([[sheetData1], [sheetData2]]) => {
        let data1 = formatSheet1(sheetData1, workDates)
        let data2 = formatSheet2(sheetData2)
        let { total, data: data3 } = updateDepartment(data1, data2)
        let { sheet1, departmentSheet, sheet2 } = generateSheet(data3, total, workDates, this.isCalcWorkDays)

        this.sheet1 = sheet1
        this.sheet2 = sheet2
        this.departmentSheet = departmentSheet
        this.isFinish = true
        this.isProcessing = false
      });
    },

    onUploadChange1 (file, fileList) {
      this.file1 = file.raw
    },

    onUploadChange2 (file, fileList) {
      this.file2 = file.raw
    },

    downloadSummarySheet () {
      save({
        name: `广州汉风---员工考勤总表${this.year}年${this.month}月份`,
        data: [
          {
            sheet: this.sheet1,
            name: `广州汉风---员工考勤表${this.year}年${this.month}月份`
          }, {
            sheet: this.sheet2,
            name: `技术部${this.month}月加班申请表`
          }
        ]
      })
    },

    downloadDepartmentSheet () {
      save({
        name: `广州汉风---部门考勤总表${this.year}年${this.month}月份`,
        data: this.departmentSheet
      })
    },
  }
}
</script>

<style>
  .upload-item {
    margin-bottom: 20px;
  }
</style>
